<template>
  <div class="home">
    <h1>Reconocedor de Tickets</h1>
    <div class="container mt-4" id="dataTable" v-bind:class="{'d-none' : !itemData}">
      <DataTable v-bind:itemDataset="itemData" @eraseItem="eraseItem($event)" v-bind:categories="categories"/>
      <br>
        <div class="input-group w-25 total">
          <label for="txtTotal"><strong>Total:</strong></label>
          <div class="input-group-prepend ml-1">
            <div class="input-group-text">$</div>
          </div>
          <input type="text" name="txtTotal" id="txtTotal" v-model="totalValue" class="form-control"/>
        </div>
        <button class="btn btn-success" @click="sendCorrectedData">Confirmar</button>
        <hr class="mb-4">
    </div>
    <div id="dataUploader" class="container">
      <InputImage v-on:send-form="sendForm" v-if="!loading"/>
      <Loader v-if="loading"/><br>
    </div>    
    <span class="alert alert-danger" v-if="error">{{ error }}</span><br>
    <span class="alert alert-info" v-if="message">{{ message }}</span>
  </div>
</template>

<script>
// @ is an alias to /src
import Loader from '../components/Loader.vue';
import InputImage from '../components/InputImageForm';
import DataTable from '../components/DataTable';
import apiEndPoint from '../settings/settings'

export default {
  name: 'home',
  components: {
    Loader,
    InputImage,
    DataTable
  },
  created(){    
    var url = apiEndPoint.toString() + "getCategories";
    fetch(url).then(response => response.json())
    .then(success => {
      this.categories = success;
    })
    .catch(error => {
      this.error = error.message + "\nApi end point: " + url;
    });
  },
  data()
  {
    return {
      loading : false,
      error : null,
      message : null,
      itemData : null,
      totalValue: 0,
      categories : null,
    }
  },
  methods : {
    sendForm(formData)
    {
      
      this.message = null;
      this.error = null;
      this.itemData = null;
      this.totalValue = 0;      
      this.loading = true;      
      fetch(apiEndPoint.toString() + 'upload', {
               // Your POST endpoint
                method: 'POST',
                // headers: {
                // // Content-Type may need to be completely **omitted**
                // // or you may need something
                // "Content-Type": "text/json"
                // },
                body: formData // This is your file object
            }).then( response => response.json()
      ).then((success) => {
        console.log(success);                
        this.itemData = success.items;
        this.totalValue = success.value;
      }).catch((error) => {
        this.error = error.message;
      }).finally( () => {
        this.loading = false;
      });
    },
    sendCorrectedData()
    {
      this.message = null;
      this.error = null;      
      var jsonData = JSON.stringify({correctionData : this.itemData, totalValue:this.totalValue });
      var options = {
        method : 'POST',
        headers: {
          'Content-Type':'application/json'
        },        
        body: jsonData
      }



      fetch(apiEndPoint.toString() + 'addNewTrainingData', options        
      ).then( response => response.json()).then(success => {
        this.message = success.message;
        this.itemData = null;
      }).catch(err => {
        this.error = err.message;
      })
    },
    eraseItem(data)
    {
      var newItemData = this.itemData.filter(element => {
        return element.id != data.id;
      })

      this.itemData = newItemData;

    }
  }
}
</script>
<style scoped>
.total
{
  margin: 0 auto;
  margin-bottom: 1rem;
}
.total label
{
  margin-top:0.5rem;
  margin-bottom:0;  
}
</style>