<template>
  <tr>
      <td>{{itemData.id}}</td>
      <td><span @mouseover="hideItem=true" v-if="!hideItem && !selected">{{itemData.name}}</span> <input v-if="hideItem || selected" @focus="selected=true" @blur="selected=false" @mouseout="hideItem=false" id="item" type="text" class="form-control" v-model="itemData.name"/></td>
      <td>
        <Autocomplete :search="search" placeholder="Seleccione una categoría" aria-label="Seleccione una categoría" v-bind:default-value="itemData.label" @submit="onSelect"/>        
      </td>
      <td>
        <button class="btn btn-danger" @click="$emit('eraseItem', itemData)"><b>X</b></button>
      </td>
  </tr>
</template>

<script>
import Autocomplete from '@trevoreyre/autocomplete-vue'

export default {  
    name:"DataItem",
    components:{
      Autocomplete
    },
    props: [ "itemData", "categories" ],
    data(){
      return{
        hideItem: false,
        selected: false,      
      }
    },
    methods:{
      search(input)
      {
        if( input.length < 1 ) { return [] }
        return this.categories.filter(category => {
          return category.toLowerCase()
          .startsWith(input.toLowerCase())
        })
      },
      onSelect(result)
      {
        if(result)
        {
          this.itemData.label = result;
        }
      }      
    }

}
</script>

<style scoped>
td
{
  vertical-align: middle;
}
span{
  width: 15rem;
  display: block;    
  margin:0 auto;  
}
input
{
  width: 15rem;  
  text-align: center;
  margin:0 auto;
}
</style>