<template>
  <div class="container">    
        <input type="file" class=" d-none" accept="image/*" @change="changeItemName" name="img" id="fileinput"/><label class="btn btn-primary" for="fileinput"><i class="material-icons">backup</i> <span>{{this.itemName}}</span></label><br>
        <button class="btn btn-success" @click="$emit('send-form', getFormData())">Enviar</button>          
  </div>
</template>

<script>

export default {
    name:"InputImageForm",
    data(){
        return{
            itemName : "Seleccione un archivo..."
        }
    },
    methods:
    {        
        getFormData(){
            var data = new FormData();
            const input = document.getElementById('fileinput');
            data.append('img',input.files[0]);            
            return data;
        },
        changeItemName()
        {
            const input = document.getElementById('fileinput');
            this.itemName = input.files[0].name;
        }
    }
}
</script>

<style scoped>
label span{
    font-weight: 600;
    display: block;    
}
</style>